a.button {
  padding-top: 4px; }

.button {
  background-color: #1565C0;
  border: none;
  color: #fff;
  height: 30px;
  width: 100%;
  max-width: 150px;
  font-weight: 500;
  font-size: 14px;
  margin: 20px auto;
  -webkit-box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.3);
          box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.3);
  display: block;
  position: relative;
  text-align: center;
  border-radius: 5px; }
  .button .fa {
    position: absolute;
    left: 15px;
    bottom: 7px; }
  .button.confirm {
    background-color: #8BC34A;
    color: white; }
  .button.danger {
    background-color: #FF5959;
    color: white; }
  .button.danger--inverted {
    background-color: white;
    color: #FF5959;
    border: 2px solid #FF5959; }
  .button.white {
    background-color: white;
    color: #1565C0;
    border: 2px solid #1565C0; }
  .button.active {
    -webkit-box-shadow: none;
            box-shadow: none;
    opacity: 1 !important; }

.button:disabled {
  opacity: 0.5; }



